import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack, Cell, Avatar } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

bridge.send("VKWebAppInit");

class App extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
      activePanel: 'main',
      userId: undefined,
      userLink: undefined,
      userName: undefined
    }}

    componentDidMount = async (e) => {
      var data = await fetch(`https://cors-anywhere.herokuapp.com/https://api.vk.com/method/users.get?user_id=390900532&v=5.107&fields=photo_100&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539`);
      var data = await data.json();
      this.setState({userId: new URLSearchParams(window.location.search).get("vk_user_id"),
                     userLink: data.response["0"].photo_100,
                     userName: data.response["0"].first_name + " " + data.response["0"].last_name
                  });
}

  render() {
  return (
    <View activePanel={this.state.activePanel}>
      <Panel id="main">
        <PanelHeader>{"Меню"}</PanelHeader>
<Div>
<Cell before={<Avatar src={this.state.userLink} />} description="Вы">{this.state.userName}</Cell>


</Div>
      </Panel>
    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
